import { Component, OnInit, OnChanges } from '@angular/core';
import { UserService } from 'src/app/service/user.service';
import { MenuItem } from 'primeng/api';
import { Router } from "@angular/router"
import { LoginService } from 'src/app/service/login.service';
import { AdminService } from 'src/app/service/admin.service';
import { Candidate } from 'src/app/models/candidate';
import { CandidateService } from 'src/app/service/candidate.service';
import { JobService } from 'src/app/service/job.service';
import { SocketIoService } from 'src/app/service/socket-io.service';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnChanges {
  conCa:any[]=['1','2','3'];
  items: MenuItem[];
  username: 'abc';
  userInfo: any;
  //-----Admin
  listCVAccept:any[]=[];
  listJobInterview:any[]=[];
  listCV:any[]=[];
  tongCVnop:any="";
  tongCVAccept:any="";
  //
  check:boolean=false;

  //---Teacher
  listRequetCV:any[]=[];
  //

  //teacher
  tongRequestCv:any="";
 
  constructor(
    private user: UserService,
    private router: Router,
    private loginService:LoginService,
    private adminService:AdminService,
    private candidateService:CandidateService,
    private jobService:JobService,
    private soketIo:SocketIoService,
    private teacher:LoginService
  ) { }

  ngOnInit() {

    this.soketIo.listen("ServerApplyJob6").subscribe(data=>
    {
      this.tongCVnop=data;
           //localStorage.setItem("duLieu",data+"");
    });
   // this.tongCVnop=localStorage.getItem("duLieu");
   
   this.soketIo.listen("ServerRequest").subscribe(data=>
    {
      this.tongRequestCv=data;
           //localStorage.setItem("duLieu",data+"");
    });

    this.soketIo.listen("ServerXacNhan").subscribe(data=>
      {
        this.tongCVAccept=data;
             //localStorage.setItem("duLieu",data+"");
      });
    
    this.items = [
      {
        label: '',
        icon: 'pi pi-fw pi-pencil',
        items: [
          {
            icon: 'pi pi-fw pi-pencil',
            label: 'Open'
          },
          {
            icon: 'pi pi-fw pi-pencil',
            label: 'Đăng Xuất',
            command: (event) => {
              localStorage.removeItem('token');
              localStorage.removeItem('user');
              this.router.navigate(['login']);
              this.user.setUserLogout();
            }
          }
        ]
      },
    ];
    
    if (this.user.getUser()) {
      this.userInfo = this.user.getUser();
      this.items[0].label = this.userInfo.userName;
    }


    
  }

  ngOnChanges() {
    debugger;
   
  }
  UngVien()
  {
    this.candidateService.SetLoggin(false);
    this.candidateService.setName("");
    this.router.navigate(['login']);
   // alert("La gi day"+this.candidateService._isLogin);
  }
  acceptCV()
  {
    
    this.jobService.listAcceptCV()
    .subscribe(res =>{
        this.listCVAccept=res;
        console.log(res);
       // this.router.navigate(['login'])
    });
  }

  getListJobInterView()
  {
    this.jobService.getJobInterview(this.candidateService.getId())
    .subscribe(res =>{
      this.listJobInterview=res;
       console.log(this.listJobInterview);
  });
  }
  
  viewListCV()
  {
    this.jobService.getListNewcvModelcs()
    .subscribe(res =>{
      this.listCV=res;
      console.log(res);
      this.listCV.reverse();
      this.soketIo.emit("Update","");
      this.tongCVnop=0;

  });
  }

  getRequestCV()
  {
    
    this.jobService.getRequestCandidate()
    .subscribe(res =>{
      this.listRequetCV=res;
      this.soketIo.emit("UpdateRequest","");
      this.tongRequestCv=0;
      console.log(this.listRequetCV);
    });
  }
  DangNhap()
  {
    this.router.navigate(['login']);
  }
  DangXuat()
  {
    localStorage.setItem("UngVien","");
    this.router.navigate(['home']);
    this.candidateService._isLogin=false;
  }


}
