import { CanActivate, Router, NavigationStart } from '@angular/router';
import { Injectable, Inject, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser, LocationStrategy } from '@angular/common';
import { Subscription } from 'rxjs';
import { CandidateService } from './candidate.service';

export let browserRefresh=false;
@Injectable()

export class AdminService implements CanActivate
{
    redirectUrl: string;
    public _isLogin=false;
    name:string;
    subscription:Subscription;
    check="";
    canActivate(){
        return this._isLogin;     
    }
    IsLogged():boolean{
        return this._isLogin;
    }
    SetLoggin(isLoggin:boolean)
    {
        this._isLogin=isLoggin;
    }
    Check(name:string,password:string):boolean
    {
        if(name=="teacher" && password=="123456")
        {
            return true;
        }
        return false;
    }
    setName(x:string)
    {
        this.name=x;
    }
    getName()
    {
        return this.name;
    }

    constructor(private candidateService:CandidateService, private router: Router,@Inject(PLATFORM_ID) private platformId: Object)
    {
        // if (isPlatformBrowser(this.platformId)) {
        //     this._isLogin = !!localStorage.getItem('name');
        //   }
          //alert(this._isLogin);
        //  alert(platformId.valueOf);
         // alert(localStorage.getItem('id'));
          //alert(localStorage.getItem('name'));

         
          this.subscription = router.events.subscribe((event) => {
            if (event instanceof NavigationStart) {
              browserRefresh = !router.navigated;
              if(browserRefresh==true)
              {
                this.check=localStorage.getItem("Admin");
                if(this.check!="")
                {
                   this._isLogin=true;
                   this.setName("Tuan");
                 //  alert(localStorage.getItem("Admin"));
                }
              }
            }
          });
        }
}